import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import styles from './_now-card-divider.scss';

const view = ({properties: {fullWidth, blockSpacing}}) => (
	<hr
		class={{
			'now-card-divider': true,
			['-' + blockSpacing]: blockSpacing,
			'-full-width': fullWidth
		}}
	/>
);

/**
 * Renders a divider within now-card content. This subcomponent is a direct descendent of `now-card`.
 *
 * ```jsx
 * <now-card-divider></now-card-divider>
 *
 * <now-card-divider full-width block-spacing='md'></now-card-divider>
 *
 * <now-card-divider block-spacing='sm'></now-card-divider>
 *
 * <now-card-divider full-width></now-card-divider>
 * ```
 *
 * @seismicElement now-card-divider
 * @summary Line that divides content within a card component.
 */
createEnhancedElement('now-card-divider', {
	properties: {
		/**
		 * Creates a full-width divider in the enclosing now-card component.
		 * @type {boolean}
		 */
		fullWidth: {default: false, schema: {type: 'boolean'}},
		/**
		 * Adds block spacing to the divider.
		 * @type {('sm'|'md'|'lg'|'none')}
		 */
		blockSpacing: {
			default: 'lg',
			schema: {type: 'string', enum: ['sm', 'md', 'lg', 'none']}
		}
	},
	view,
	styles
});
