import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import styles from './_now-card-footer.scss';

const _renderSlotContent = (label, split) => {
	if (label && typeof label === 'string') {
		return (
			<p className="now-card-footer-p">
				<span className="now-line-height-crop">
					<span className="now-card-footer-label" title={label}>
						{label}
					</span>
				</span>
			</p>
		);
	} else {
		return (
			<Fragment>
				<div
					class={{
						'now-card-footer-slot': true,
						'-start': true,
						'-half': split !== 'unequal',
						'-three-quarter': split === 'unequal'
					}}>
					<slot name="start">
						<p className="now-card-footer-p">
							<span className="now-line-height-crop">
								<span
									className="now-card-footer-label"
									title={label && label.start ? label.start : undefined}>
									{label && label.start ? label.start : undefined}
								</span>
							</span>
						</p>
					</slot>
				</div>
				<div
					class={{
						'now-card-footer-slot': true,
						'-end': true,
						'-half': split !== 'unequal',
						'-one-quarter': split === 'unequal'
					}}>
					<slot name="end">
						<p className="now-card-footer-p">
							<span className="now-line-height-crop">
								<span
									className="now-card-footer-label"
									title={label && label.end ? label.end : undefined}>
									{label && label.end ? label.end : undefined}
								</span>
							</span>
						</p>
					</slot>
				</div>
			</Fragment>
		);
	}
};

const view = (state) => {
	const {label, split} = state.properties;
	return (
		<footer className="now-card-footer">
			<slot>{_renderSlotContent(label, split)}</slot>
		</footer>
	);
};

/**
 * Footer subcomponent for custom cards. Displays text content or more
 * complex slotted content. Either displayed as the full width of the card
 * or subdivided into a two-column display of equal or unequal widths.
 *
 * ```jsx
 * <now-card-footer label="Updated 2019-01-15 08:41:09" />
 *
 * <now-card-footer label={{start: 'Half width', end: 'Half width'}} />
 *
 * <now-card-footer split="unequal">
 *   <now-highlighted-value slot="start" label="Start Label" status="high" show-icon />
 *   <now-highlighted-value slot="end" label="End Label" status="high" show-icon />
 * </now-card-footer>
 * ```
 *
 * @seismicElement now-card-footer
 * @summary Footer subcomponent for custom cards
 */
createEnhancedElement('now-card-footer', {
	properties: {
		/**
		 * Label(s) for the card footer.
		 *
		 * Accepts a string for the full width layout.
		 *
		 * Accepts an object for the split layouts.
		 * `start` - The label at the start (left for LTR languages) of the footer in a split layout.
		 * `end` - The label at the end (right for LTR languages) of the footer in a split layout.
		 * @type {(string|{start: string, end: string})}
		 */
		label: {
			schema: {
				oneOf: [
					{
						type: 'object',
						properties: {start: {type: 'string'}, end: {type: 'string'}},
						additionalProperties: false
					},
					{type: 'string'}
				]
			}
		},
		/**
		 * `equal` - Two containers, each 50% of the card's width.
		 * `unequal` - Two containers, start (left for LTR languages) 75% of the card's width
		 * and end (right for LTR languages) 25% of the card's width
		 * @type {('equal'|'unequal')}
		 */
		split: {
			default: 'equal',
			schema: {type: 'string', enum: ['equal', 'unequal']}
		}
	},
	view,
	styles,
	slots: {
		/**
		 * Slot for full width footer.
		 * @defaultSlot
		 */
		defaultSlot: {},
		/**
		 * Slot for block-start (left for LTR languages) content with split layout.
		 */
		start: {},
		/**
		 * Slot for block-end (right for LTR languages) content with split layout.
		 */
		end: {}
	}
});
