import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-icon';
import '@servicenow/now-button';
import '@servicenow/now-dropdown';
import styles from './_now-card-header.scss';
import {itemsSchema} from '@servicenow/now-dropdown';
import {t} from 'sn-translate';

// Used to keep track of the original, consumer-provided item in cases where we
// need to append more info (like a unique ID) to that item.
const originalItem = Symbol('original item');

/**
 * Ensures all actions have an `id` property to conform to the dropdown schema.
 * @param {string} prefix - prefix to add to any generated action ID.
 * @param {Array<DropdownItem>} actions - the actions array to check for IDs.
 * @return {Array<DropdownItem>} the unmodified `actions` array if all actions
 * already contain an ID. Otherwise, returns a copy of the `actions` array with
 * an ID for all containing items.
 */
const addIds = (prefix, actions) => {
	const needIds = actions.reduce((result, action, index) => {
		if (!('id' in action)) {
			result.push(index);
		}

		return result;
	}, []);

	// Return the exact same object if we can
	if (!needIds.length) {
		return actions;
	}

	const actionsWithIds = [...actions];
	for (const index of needIds) {
		const action = actionsWithIds[index];
		actionsWithIds[index] = {
			...action,
			id: `${prefix}-${index}`,
			[originalItem]: action
		};
	}

	return actionsWithIds;
};

const renderDropdowns = (dropdowns, size, openDropdowns) => {
	if (!dropdowns) {
		return undefined;
	}
	return dropdowns.map((dropdown) => {
		return (
			<now-dropdown-custom-target
				class={{'now-card-header-actions': true, ['-' + size]: size}}
				items={dropdown.items}
				disabled={dropdown.disabled}
				select="none"
				append-to-payload={{dropdownId: dropdown.id}}>
				<now-button-iconic
					slot="trigger"
					icon={dropdown.icon}
					tooltip-content={dropdown.label}
					bare
					size={size}
					hide-padding={size === 'md' ? true : undefined}
					variant="tertiary"
					config-aria={{
						'aria-label': dropdown.label,
						'aria-haspopup': 'listbox',
						'aria-expanded': openDropdowns[dropdown.id] ? 'true' : 'false'
					}}
				/>
			</now-dropdown-custom-target>
		);
	});
};

const renderActions = (
	dropdowns,
	actions,
	dispatch,
	size,
	actionDropdownOpen,
	componentId
) => {
	if (!actions) {
		return;
	}
	if (
		(!dropdowns && actions.length < 4) ||
		(dropdowns && dropdowns.length + actions.length < 4)
	) {
		return actions.map((action) => {
			return (
				<now-button-iconic
					class={{'now-card-header-actions': true, ['-' + size]: size}}
					icon={action.icon}
					disabled={action.disabled}
					variant="tertiary"
					bare
					size={size}
					hide-padding={size === 'md' ? true : undefined}
					tooltip-content={action.label}
					config-aria={{
						'aria-label': action.label
					}}
					on-click={() => {
						if (!action.disabled) {
							dispatch(() => {
								return {
									type:
										action.clickActionType || 'NOW_CARD_HEADER#ACTION_CLICKED',
									payload: {action: action}
								};
							});
						}
					}}
				/>
			);
		});
	} else {
		return (
			<now-dropdown-custom-target
				class={{'now-card-header-actions': true, ['-' + size]: size}}
				items={addIds(componentId, actions)}
				select="none"
				append-to-payload={{actionDropdown: true}}>
				<now-button-iconic
					slot="trigger"
					tooltip-content={t('Card Actions')}
					icon="ellipsis-v-outline"
					bare
					hide-padding={size === 'md' ? true : undefined}
					size={size}
					variant="tertiary"
					config-aria={{
						'aria-label': t('Card Actions'),
						'aria-haspopup': 'listbox',
						'aria-expanded': actionDropdownOpen ? 'true' : 'false'
					}}
				/>
			</now-dropdown-custom-target>
		);
	}
};

const renderCardInteraction = (
	dropdowns,
	actions,
	dispatch,
	size,
	openDropdowns,
	actionDropdownOpen,
	componentId
) => {
	return (
		<Fragment>
			<div className="now-card-header-metadata">
				<slot name="metadata" />
			</div>
			{renderDropdowns(dropdowns, size, openDropdowns)}
			{renderActions(
				dropdowns,
				actions,
				dispatch,
				size,
				actionDropdownOpen,
				componentId
			)}
		</Fragment>
	);
};

const renderTagLine = (
	tagline,
	dropdowns,
	dispatch,
	actions,
	openDropdowns,
	actionDropdownOpen,
	componentId
) => {
	if (tagline) {
		const {label, icon, variant = 'tertiary'} = tagline;
		return (
			<div
				class={{
					'now-card-tagline': true,
					['-' + variant]: variant
				}}
				title={label}>
				{icon ? (
					<now-icon className="now-m-inline-end--xs" icon={icon} size="sm" />
				) : (
					undefined
				)}
				<p className="now-card-tagline-p">
					<span className="now-line-height-crop">
						<span className="now-card-tagline-label" title={label}>
							{label}
						</span>
					</span>
				</p>
				{renderCardInteraction(
					dropdowns,
					actions,
					dispatch,
					'sm',
					openDropdowns,
					actionDropdownOpen,
					componentId
				)}
			</div>
		);
	}
};

const renderHeading = (heading) => {
	if (heading) {
		const {
			label,
			level = 3,
			size = 'sm',
			variant = 'primary',
			lines = 1
		} = heading;

		const HeadingLevel = `h${level}`;
		return (
			<HeadingLevel
				class={{
					'now-card-header-heading': true,
					['-' + size]: true,
					['-' + variant]: true,
					'show-one-line': lines === 1,
					'show-two-lines': lines === 2
				}}
				title={label}>
				<span className="now-line-height-crop">
					<span className="now-card-header-heading-label">{label}</span>
				</span>
			</HeadingLevel>
		);
	}
};

const renderCaption = (caption) => {
	if (caption) {
		const {label, size = 'sm', lines = 1} = caption;
		return (
			<p
				class={{
					'now-card-header-caption': true,
					['-' + size]: true,
					'show-one-line': lines === 1,
					'show-two-lines': lines === 2
				}}
				title={label}>
				<span className="now-line-height-crop">
					<span className="now-card-header-caption-label">{label}</span>
				</span>
			</p>
		);
	}
};

const renderHeadingContainer = (
	tagline,
	heading,
	caption,
	dropdowns,
	actions,
	dispatch,
	openDropdowns,
	actionDropdownOpen,
	componentId
) => {
	if (heading) {
		const topAlign =
			(heading && heading.lines === 2) || (caption && caption.lines === 2);
		return (
			<header
				class={{
					'now-card-header': true,
					'-block-center': !topAlign,
					'now-m-block-start--sm': tagline && (tagline.label || tagline.icon)
				}}>
				<div className="now-card-header-identifier">
					<slot name="identifier" />
				</div>
				<div className="now-card-header-headings">
					{renderHeading(heading)}
					{renderCaption(caption)}
				</div>
				{tagline && (tagline.label || tagline.icon)
					? undefined
					: renderCardInteraction(
							dropdowns,
							actions,
							dispatch,
							'md',
							openDropdowns,
							actionDropdownOpen,
							componentId
					  )}
			</header>
		);
	}
};

const view = (state, {dispatch}) => {
	const {tagline, heading, caption, dropdowns, actions} = state.properties;
	const {openDropdowns, actionDropdownOpen, componentId} = state;

	return (
		<Fragment>
			{renderTagLine(
				tagline,
				dropdowns,
				dispatch,
				actions,
				openDropdowns,
				actionDropdownOpen,
				componentId
			)}
			{renderHeadingContainer(
				tagline,
				heading,
				caption,
				dropdowns,
				actions,
				dispatch,
				openDropdowns,
				actionDropdownOpen,
				componentId
			)}
		</Fragment>
	);
};

/**
 * Header subcomponent for custom cards. Highly configurable with multi-line
 * headings and captions, a tagline for categories and identifiers, and the
 * option to include multiple dropdowns and/or action menus.
 *
 * ```jsx
 * <now-card-header heading={{label: 'Title for the Card'}} />
 *
 * <now-card-header
 *   tagline={{label: 'Tagline for the card'}}
 *   heading={{label: 'Title for the card', size: 'md', lines: 2}}
 *   caption={{label: 'Caption for the card', lines: 2}}
 *   dropdowns={[{id: 'dropdown1', items: [{id: 'dropItem1', label: 'Action One'}]}]}
 *   actions={[{id: 'action1', icon: 'download-outline', label: 'Download'}]}>
 *   <now-icon slot="identifier" icon="document-outline" />
 * </now-card-header>
 * ```
 *
 * @seismicElement now-card-header
 * @summary Header subcomponent for custom cards.
 */
createEnhancedElement('now-card-header', {
	properties: {
		/**
		 * An object with properties to configure a tagline for the card:
		 * - `label` - Text context for the tagline.
		 * - `icon` - Icon that appears before the tagline's `label`.
		 * - `variant = 'tertiary'` - Defines the color of the icon and label.
		 * @type {{label: string, icon: string, variant: ('secondary'|'tertiary')}}
		 */
		tagline: {
			schema: {
				type: 'object',
				properties: {
					label: {type: 'string'},
					icon: {type: 'string'},
					variant: {type: 'string', enum: ['secondary', 'tertiary']}
				}
			}
		},
		/**
		 * An object with properties to configure a heading for the card:
		 * - `label` - Text context for the heading.
		 * - `lines = 1` - Number of lines the heading will extend to before truncating.
		 * - `level = 3` - HTML heading element number used to render the heading.
		 * - `size = 'sm'` - Size of the heading.
		 * - `variant = 'primary'` - Visual styling for the heading.
		 * @type {{label: string, lines: (1|2), level: (1|2|3|4|5|6), size: ('sm'|'md'|'lg'), variant: ('primary'|'secondary'|'tertiary')}}
		 */
		heading: {
			schema: {
				type: 'object',
				properties: {
					label: {type: 'string'},
					lines: {type: 'number', enum: [1, 2]},
					level: {type: 'number', enum: [1, 2, 3, 4, 5, 6]},
					size: {type: 'string', enum: ['sm', 'md', 'lg']},
					variant: {type: 'string', enum: ['primary', 'secondary', 'tertiary']}
				}
			}
		},
		/**
		 * An object with properties to configure an optional caption for the card:
		 * - `label` - Text content for the caption.
		 * - `lines = 1` - Number of lines the caption will extend to before truncating.
		 * - `size = 'sm'` - Size of the caption.
		 * @type {{label: string, lines: (1|2), size: ('sm'|'md')}}
		 */
		caption: {
			schema: {
				type: 'object',
				properties: {
					label: {type: 'string'},
					lines: {type: 'number', enum: [1, 2]},
					size: {type: 'string', enum: ['sm', 'md']}
				}
			}
		},
		/**
		 * Array that defines one or more action dropdowns.
		 * Displayed before `actions` in the card header.
		 *
		 * - `id` required - An id for the dropdown used to correctly update `aria-expanded`.
		 * - `label` required - Description of the dropdown used as the tooltip content and `aria-label` of the dropdown.
		 * - `items` required - Array of objects describing each item in the dropdown.
		 * - `disabled` - Set to `true` to disable the dropdown trigger.
		 * - `icon` - Icon that appears in the dropdown trigger.
		 *
		 * See `now-dropdown` documentation for the expected structure of a DropdownItem object.
		 * @type {{id: (string|number), label: string, items: DropdownItem[], disabled: boolean, icon: string}[]}
		 */
		dropdowns: {
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						id: {type: ['string', 'number']},
						label: {type: 'string'},
						items: itemsSchema,
						icon: {type: 'string'},
						disabled: {type: 'boolean'}
					}
				}
			}
		},
		/**
		 * Array of objects that defines the iconic buttons displayed after the
		 * heading and caption. By default, buttons will render from
		 * right to left (for LTR languages). More than three buttons will be rendered in a dropdown.
		 *
		 * The keys of each object are properties for both `now-button-iconic` and
		 * `now-dropdown`. See `now-button-iconic` or `now-dropdown` for more
		 * information on using these properties.
		 * - `icon` required - Icon for the action.
		 * - `label` required - Description of the action used for the tooltip content and `aria-label`.
		 * - `disabled` - Set to `true` to mute the button color and disable user click interactions.
		 * - `clickActionType = "NOW_CARD_HEADER#ACTION_CLICKED"` - Overrides the default action type dispatched when the user clicks on the action.
		 * @type {{icon: string, label: string, disabled: boolean, clickActionType: string}[]}
		 */
		actions: {
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						icon: {type: 'string'},
						label: {type: 'string'},
						disabled: {type: 'boolean'},
						clickActionType: {type: 'string'}
					}
				}
			}
		}
	},
	view,
	styles,
	initialState: {
		openDropdowns: {},
		actionDropdownOpen: false
	},
	actionHandlers: {
		'NOW_DROPDOWN_CUSTOM_TARGET#ITEM_CLICKED': {
			stopPropagation: true,
			effect: ({action, dispatch}) => {
				const item = action.payload.item[originalItem] || action.payload.item;
				const actionType =
					item.clickActionType || 'NOW_CARD_HEADER#ACTION_CLICKED';

				dispatch(() => {
					return {
						type: actionType,
						payload: {
							action: {
								...item,
								dropdownId: action.payload.dropdownId
							}
						}
					};
				});
			}
		},
		'NOW_DROPDOWN_CUSTOM_TARGET#OPENED_SET': ({action, updateState}) => {
			if (action.payload.actionDropdown) {
				updateState({actionDropdownOpen: action.payload.value});
			} else {
				updateState((state) => {
					return {
						value: {
							openDropdowns: {
								...state.openDropdowns,
								[action.payload.dropdownId]: action.payload.value
							}
						},
						operation: 'merge'
					};
				});
			}
		}
	},
	dispatches: {
		/**
		 * Dispatched when the user clicks an action button or selects an
		 * action from the action dropdown.
		 * Payload will include a reference to the clicked action's configuration as
		 * well as the dropdown id, if specified. Action type can be overridden
		 * by setting `action.clickActionType`.
		 *
		 * @type {{action: Object, dropdownId: (string|number)}}
		 */
		'NOW_CARD_HEADER#ACTION_CLICKED': {}
	},
	slots: {
		/**
		 * Slot for the identifier at the beginning of the heading and caption.
		 */
		identifier: {},
		/**
		 * Slot for metadata such as a badge, highlighted-value, or string.
		 * Displayed after the heading and before the dropdown and actions.
		 */
		metadata: {}
	}
});
