import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {
	filterAriaAttributes,
	isFocusable
} from '@servicenow/library-enhanced-utils';
import styles from './_now-card.scss';
import focusBehavior from '@servicenow/behavior-focus';
import {configAriaSchema} from '@servicenow/library-schemas';

const elementIsFocusable = (evt) => {
	const composedPath = evt.composedPath();
	const currentTarget = evt.currentTarget;
	for (let i = 0; i < composedPath.length; i++) {
		if (composedPath[i] === currentTarget) {
			return false;
		}
		if (isFocusable(composedPath[i])) {
			return true;
		}
	}
};

const view = (state, {dispatch, updateState}) => {
	const {size, interaction, selected, configAria, sidebar} = state.properties;
	const color = sidebar && sidebar.color;
	const variant = sidebar && sidebar.variant;
	const {pressed} = state;

	const onClick = () => {
		{
			interaction === 'click'
				? dispatch(() => {
						return {
							type: 'NOW_CARD#CLICKED',
							payload: {}
						};
				  })
				: dispatch(({properties}) => ({
						type: 'NOW_CARD#SELECTED_SET',
						payload: {value: !properties.selected}
				  }));
		}
	};

	const slotOnClick = (evt) => {
		if (!elementIsFocusable(evt)) {
			evt.currentTarget.previousElementSibling.focus();
			onClick();
		}
	};

	const slotOnPress = (evt) => {
		if (!elementIsFocusable(evt)) {
			updateState({pressed: true});
		}
	};

	const slotOnRelease = (evt) => {
		if (!elementIsFocusable(evt)) {
			updateState({pressed: false});
		}
	};

	return interaction === 'none' ? (
		<article
			class={{
				'now-card': true,
				'now-card-slot': true,
				['-' + size]: size,
				'has-sidebar': sidebar,
				['-' + color]: color,
				['-' + variant]: variant
			}}>
			<slot />
		</article>
	) : (
		<article class={{nowCardContainer: true}}>
			<button
				class={{
					'now-card': true,
					'is-interactive': true,
					'is-selected': selected,
					'is-pressed': pressed,
					'has-sidebar': sidebar,
					['-' + color]: color,
					['-' + variant]: variant
				}}
				type="button"
				{...filterAriaAttributes(configAria, 'button')}
				aria-pressed={
					interaction === 'select' ? (selected ? 'true' : 'false') : undefined
				}
				on-click={onClick}
			/>
			<div
				class={{
					'now-card-slot': true,
					'is-interactive': true,
					'is-selected': selected,
					['-' + size]: size
				}}
				on-click={slotOnClick}
				on-mousedown={slotOnPress}
				on-mouseup={slotOnRelease}
				on-mouseleave={slotOnRelease}>
				<slot />
			</div>
		</article>
	);
};

/**
 * A container for specific information in a simple, easily scannable format.
 * Provides a border, card-level interactivity, and a default slot for holding
 * card subcomponents.
 *
 * ```jsx
 * <now-card size="lg" interaction="select">
 *   <now-card-header ... />
 *   <now-card-footer ... />
 * </now-card>
 * ```
 *
 * @seismicElement now-card
 * @summary A container for specific information in a simple, easily scannable format.
 */
createEnhancedElement('now-card', {
	properties: {
		/**
		 * Defines the card's size.
		 * @type {('sm'|'md'|'lg')}}
		 */
		size: {default: 'lg', schema: {type: 'string', enum: ['sm', 'md', 'lg']}},
		/**
		 * `none`: Card has no actionable state.
		 * `click`: Card can be clicked. Fires `NOW_CARD#CLICKED` when clicked.
		 * `select`: Card can be selected. Fires `NOW_CARD#SELECTED_SET` when selected.
		 * @type {('none'|'click'|'select')}
		 */
		interaction: {
			default: 'none',
			schema: {type: 'string', enum: ['none', 'click', 'select']}
		},
		/**
		 * Indicates the selected state of the card. Should only be used with `interaction='select'`.
		 * Maps to `aria-pressed`.
		 * Use `manage-selected` to override the default behavior and handle the
		 * `NOW_CARD#SELECTED_SET` action manually.
		 * @type {boolean}
		 */
		selected: {default: false, manageable: true, schema: {type: 'boolean'}},
		/**
		 * An object whose keys reference a specific HTML element within the
		 * now-card. The value of each key is an object that contains an ARIA
		 * property (or properties) and corresponding value/s. These ARIA
		 * properties are set on the specified element.
		 * The 'button' key corresponds to the inner html `<button>` in
		 * now-card (if `interaction` type is not `none`).
		 * See https://www.w3.org/TR/wai-aria-1.1/#button for properties and accepted values.
		 * @type {{ 'aria-*': string }}
		 */
		configAria: {
			schema: {
				oneOf: [
					{
						type: 'object',
						properties: {
							button: configAriaSchema
						},
						additionalProperties: false
					},
					configAriaSchema
				]
			}
		},
		/**
		 * Specifies the color of the sidebar that appears along the side of
		 * the card. The value is an object with values for `color` and `variant`.
		 * @type {{color: ('critical'|'high'|'warning'|'moderate'|'positive'|'info'|'low'|'magenta'|'pink'|'orange'|'yellow'|'brown'|'green'|'green-yellow'|'blue'|'gray'|'teal'|'purple'), variant: ('primary'|'secondary'|'tertiary')}}
		 */
		sidebar: {
			schema: {
				type: 'object',
				properties: {
					color: {
						type: 'string',
						enum: [
							'critical',
							'high',
							'warning',
							'moderate',
							'positive',
							'info',
							'low',
							'magenta',
							'pink',
							'orange',
							'yellow',
							'brown',
							'green',
							'green-yellow',
							'blue',
							'gray',
							'teal',
							'purple'
						]
					},
					variant: {type: 'string', enum: ['primary', 'secondary', 'tertiary']}
				}
			}
		}
	},
	initialState: {
		pressed: false
	},
	view,
	styles,
	behaviors: [
		{
			behavior: focusBehavior
		}
	],
	slots: {
		/**
		 * `now-card` is a wrapper for the `defaultSlot`.
		 * @defaultSlot
		 */
		defaultSlot: {}
	},
	dispatches: {
		/**
		 * Dispatched when the card is clicked when `interaction` is set to `click`.
		 * @type {{}}
		 */
		'NOW_CARD#CLICKED': {},
		/**
		 * Dispatched when the user toggles the selected status of the card when
		 * `interaction` is set to `select`.
		 * Set the `manage-selected` property to override the default behavior and
		 * handle this action manually.
		 * @type {{value: boolean}}
		 */
		'NOW_CARD#SELECTED_SET': {}
	}
});
