import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import '@servicenow/now-button';
import styles from './_now-card-actions.scss';

const _renderButtons = (itemsObj, dispatch) => {
	if (itemsObj) {
		return itemsObj.map((item) => {
			return (
				<now-button
					className="now-card-actions-button"
					size="sm"
					variant={item.variant}
					label={item.label}
					icon={item.icon}
					disabled={item.disabled}
					on-click={() => {
						if (!item.disabled) {
							dispatch(
								item.clickActionType || 'NOW_CARD_ACTIONS#ACTION_CLICKED',
								{
									action: item
								}
							);
						}
					}}
				/>
			);
		});
	} else {
		return '';
	}
};

const view = (state, {dispatch}) => {
	const {items} = state.properties;
	const itemsObj = items ? items : undefined;
	return (
		<div className="now-card-actions">{_renderButtons(itemsObj, dispatch)}</div>
	);
};

/**
 * Card subcomponent that enables the user to take action. Use card actions
 * to display one to three large action buttons for a card.
 *
 * ```jsx
 * const cardActions = [
 *   {label: 'Decline', variant: 'secondary-negative'},
 *   {label: 'Approve', variant: 'secondary-positive'}
 * ];
 * <now-card-actions items={cardActions} />
 * ```
 *
 * @seismicElement now-card-actions
 * @summary Card subcomponent that enables the user to take action.
 */
createEnhancedElement('now-card-actions', {
	properties: {
		/**
		 * An array of objects that defines the buttons displayed.
		 * The keys for each object are properties for `now-button`. See
		 * `now-button` for more information on using these properties.
		 * - `label: string` required - Label describing the action.
		 * - `icon: string` - Icon displayed with the label.
		 * - `variant: string` - Button style variant.
		 * - `disabled: boolean` - Set to `true` to mute the button color and disable user click interactions.
		 * - `clickActionType: string = "NOW_CARD_ACTIONS#ACTION_CLICKED"` - Overrides the default action type dispatched when the user clicks the action
		 * @type {{ label: string, icon: string, variant: string, disabled: boolean, clickActionType: string}[]}
		 */
		items: {
			required: true,
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						label: {type: 'string'},
						icon: {type: 'string'},
						// TODO - re-use a typedef from now-button for this?
						variant: {type: 'string'},
						disabled: {type: 'boolean'},
						clickActionType: {type: 'string'}
					}
				}
			}
		}
	},
	view,
	styles,
	dispatches: {
		/**
		 * Dispatched when the user clicks a button. The payload will include a
		 * reference to the clicked item and action. The action type can be overridden
		 * by configuring `item.clickActionType`.
		 * @type {{action: Object}}
		 */
		'NOW_CARD_ACTIONS#ACTION_CLICKED': {}
	}
});
